import React from "react";
import { makeStyles } from "@material-ui/core/styles";
import Button from "@material-ui/core/Button";
import Fab from "@material-ui/core/Fab";
import AddIcon from "@material-ui/icons/Add";
import EditIcon from "@material-ui/icons/Edit";
import FavoriteIcon from "@material-ui/icons/Favorite";
import NavigationIcon from "@material-ui/icons/Navigation";
import Typography from "@material-ui/core/Typography";
import Slider from "@material-ui/core/Slider";
import Accordion from "@material-ui/core/Accordion";
import AccordionSummary from "@material-ui/core/AccordionSummary";
import AccordionDetails from "@material-ui/core/AccordionDetails";
import ExpandMoreIcon from "@material-ui/icons/ExpandMore";
import TextField from "@material-ui/core/TextField";
import LinearProgress from "@material-ui/core/LinearProgress";

const useStyles = makeStyles((theme) => ({
  button: {
    margin: "auto",
    minWidth: "600px",
    textAlign: "center",
    "& > *": {
      margin: theme.spacing(1)
    }
  },
  extendedIcon: {
    marginRight: theme.spacing(1)
  },
  slider: {
    width: 300,
    margin: "auto",
    marginBottom: "20px"
  },
  acordion: {
    width: "100%"
  },
  heading: {
    fontSize: theme.typography.pxToRem(15),
    fontWeight: theme.typography.fontWeightRegular
  },
  TextField: {
    "& > *": {
      margin: theme.spacing(1),
      width: "25ch"
    }
  },
  loading: {
    width: "100%"
  }
}));

function valuetext(value) {
  return `${value}°C`;
}

export default function ContainedButtons() {
  const classes = useStyles();
  const [progress, setProgress] = React.useState(0);

  React.useEffect(() => {
    const timer = setInterval(() => {
      setProgress((oldProgress) => {
        if (oldProgress === 100) {
          return 0;
        }
        const diff = Math.random() * 10;
        return Math.min(oldProgress + diff, 100);
      });
    }, 500);

    return () => {
      clearInterval(timer);
    };
  }, []);

  return (
    <div className={classes.button}>
      <Button variant="contained">ニュース</Button>
      <Button variant="contained" color="primary">
        サイト一覧
      </Button>
      <Button variant="contained" color="secondary">
        お問い合わせ
      </Button>
      <div className={classes.slider}>
        <Slider
          defaultValue={0.00000005}
          getAriaValueText={valuetext}
          aria-labelledby="discrete-slider-small-steps"
          step={0.00000001}
          marks
          min={-0.00000005}
          max={0.0000001}
          valueLabelDisplay="auto"
        />
      </div>
      <h2>サイト紹介</h2>
      <div className={classes.acordion}>
        <Accordion>
          <AccordionSummary
            expandIcon={<ExpandMoreIcon />}
            aria-controls="panel1a-content"
            id="panel1a-header"
          >
            <Typography className={classes.heading}>その1</Typography>
          </AccordionSummary>
          <AccordionDetails>
            <Typography>material-uiで作りました。</Typography>
          </AccordionDetails>
        </Accordion>
      </div>
      <form className={classes.textField} noValidate autoComplete="off">
        <TextField id="outlined-basic" label="名前を入力" variant="outlined" />
      </form>
      <div className={classes.root}>
        <LinearProgress variant="determinate" value={progress} />
      </div>
    </div>
  );
}
